    @charset "UTF-8";
    /* -------------------
  COMMON
--------------------*/
    
    body {
        font-family: $font-base;
        font-weight: 500;
        &.fixed {
            height: 100vh;
            overflow: hidden;
            // display: flex;
        }
    }
    
    // =========================================
    // CONTROL VIEW ELEMENT
    // =========================================
    .pc-only {
        // display: block;
        // @include mq() {
        //     display: none !important;
        // }
    }
    
    .pc-only2 {
        display: block;
        @include mq(sm2) {
            display: none !important;
        }
    }
    
    .pc-onlyt {
        display: block;
        @include mq(smt) {
            display: none !important;
        }
    }
    
    .pc-only3 {
        display: inline-block;
        @include mq() {
            display: none !important;
        }
    }
    
    .sp-only {
        display: none !important;
        @include mq() {
            display: block !important;
        }
    }
    
    .sp-only2 {
        display: none !important;
        @include mq(sm2) {
            display: block !important;
        }
    }
    
    .sp-onlyt {
        display: none !important;
        @include mq(smt) {
            display: block !important;
        }
    }
    
    // =========================================
    // FONT FAMILY
    // =========================================
    .en {
        font-family: $font-a;
    }
    
    // =========================================
    // WRAPPER
    // =========================================
    .wrapper {
        max-width: 1400px;
        margin: auto;
        padding: 10px;
        @include mq() {
            // min-width: 100%;
            // padding-top: 50px;
        }
    }
    
    // =========================================
    // MAIN
    // =========================================
    .main {
        // padding-bottom: 80px;
        // background-color: #F7F6ED;
    }
    
    // =========================================
    // CONTAINER
    // =========================================
    .container {
        position: relative;
        width: 1040px;
        margin: 0 auto;
        @include mq() {
            width: 100%;
            padding: 0 15px;
        }
    }
    
    .container2 {
        position: relative;
        width: 900px;
        margin: 0 auto;
        @include mq() {
            width: 100%;
            padding: 0 15px;
        }
    }
    
    .d-flex {
        display: flex;
    }
    
    .position-relative {
        position: relative;
    }
    
    p {
        font-family: $font-base;
        color: $color-main;
    }
    
    .tab:not(:target) {
        display: none;
    }
    
    .tab:last-child {
        display: block;
    }
    
    .tab:target~.tab:last-child {
        display: none;
    }
    
    .nav-tabs {
        display: flex;
        li {
            a {
                padding: 5px;
                display: flex;
                align-items: center;
                font-family: $font-a;
                font-weight: bold;
                font-size: fs(15);
                line-height: 16px;
                color: black;
                background: #f0f0f0;
                border: 1px solid #d1c9ae;
                &:hover {
                    background: #f1e9d0;
                    border: 1px solid #d1c9ae;
                }
                &.active {
                    background: #f1e9d0;
                    border: 1px solid #d1c9ae;
                }
                img {
                    margin-right: 10px;
                }
                span {
                    font-family: $font-base;
                    font-weight: 500;
                    font-size: 12px;
                    line-height: 16px;
                    text-align: left;
                    color: $color-main;
                }
            }
        }
    }
    
    @media screen and (min-width: 980px) {
        #topcontrol {
            z-index: 200;
            right: 30px !important;
            bottom: 30px !important;
            span {}
        }
        #topcontrol span {
            display: none;
        }
    }
    
    #topcontrol {
        z-index: 200;
        right: 30px !important;
        bottom: 30px !important;
        @include mq(sm2) {
            bottom: 0 !important;
            left: 0;
            right: 0;
            width: 100%;
            background-color: rgba( 0, 0, 0, 0.7);
            border-top: 1px solid #FFF;
            color: #FFF;
            text-align: center;
            img {
                display: none;
            }
            span {
                display: block;
                line-height: 80px;
            }
        }
    }
    
    @media screen and (min-width: 980px) {
        #topcontrol span {
            display: none;
        }
    }
    
    .btn_more {
        margin: 1em 0;
        text-align: right;
        font-size: 14px;
        font-weight: bold;
        a {
            background: url(../images/common/ar_more_long.png) no-repeat right 10px center;
            display: inline-block;
            padding: 0 70px 0 0;
            margin-right: 15px;
            line-height: 25px;
            text-decoration: none;
            color: #000;
            transition: 0.3s ease-in-out;
            &:hover {
                background-position: right 0 center;
            }
        }
    }
    
    .bold {
        font-weight: bold;
    }
    
    #main-wrapper section {
        padding: 70px 20px;
        @include mq(sm2) {
            padding: 30px 0px 20px;
        }
    }
    
    #recently {
        background: #f3fafb;
        padding: 10px 40px 30px !important;
        .slick-prev,
        .slick-next {
            position: absolute;
            display: block;
            height: 25px;
            width: 25px;
            line-height: 0px;
            font-size: 0px;
            cursor: pointer;
            background: transparent;
            color: transparent;
            top: 50%;
            -webkit-transform: translate(0, -50%);
            -ms-transform: translate(0, -50%);
            transform: translate(0, -50%);
            padding: 0;
            border: none;
            outline: none;
        }
        .slick-prev {
            background: url(../images/common/ar_left_blue.svg) no-repeat;
            text-indent: -9999px;
            left: -35px;
        }
        .slick-next {
            background: url(../images/common/ar_right_blue.svg) no-repeat;
            text-indent: -9999px;
            right: -35px;
        }
    }
    
    .lv_h3 {
        font-size: 20px;
        font-weight: 600;
        margin-bottom: 20px;
        margin-top: 30px;
    }
    
    .pc-only {
        display: block;
    }
    
    @media only screen and (max-width: 950px) {
        .pc-only {
            display: none !important;
        }
    }