header {
    i {
        margin-right: 5px;
    }
    .header {
        &-top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            .logo {
                display: block;
                p {
                    font-size: fs(12);
                    line-height: 16px;
                    text-align: left;
                    color: #0c0c0c;
                    font-family: $font-a;
                }
                @include mq(smt) {
                    width: 120px;
                }
            }
            a:hover {
                opacity: .7;
            }
            &>div {
                display: flex;
                align-items: end;
            }
        }
        &-mypage {
            padding-left: 10px;
            .btn {
                &-login {
                    border: 0.5px solid #d5d5d5;
                    border-radius: 3px;
                    padding: 3px 5px;
                    background: linear-gradient(#fff 0%, #bfbfbf 100%);
                    text-align: center;
                    font-family: $font-a;
                    font-size: fs(12);
                }
                &-register {
                    border: 0.5px solid #d5d5d5;
                    border-radius: 3px;
                    padding: 3px 5px;
                    background: linear-gradient(#fff 0%, #bfbfbf 100%);
                    font-family: $font-a;
                    font-weight: 500;
                    font-size: fs(12);
                    line-height: 16px;
                    color: $color-main;
                    span {
                        margin-left: 5px;
                        color: red;
                    }
                }
            }
            ul {
                margin-top: 5px;
                display: flex;
                align-items: center;
            }
            ul li {
                border-right: 1px solid $color-main;
                font-family: $font-a;
                font-weight: 500;
                font-size: 12px;
                line-height: 16px;
                color: $color-main;
                &:last-child {
                    border-right: none;
                }
                a {
                    padding: 0 5px;
                }
            }
            select {
                border: none;
            }
        }
        &-top-right {
            a {
                margin-right: 10px;
                border-radius: 5px;
                padding: 6px 12px;
                color: white;
                font-family: $font-a;
                font-weight: 500;
                font-size: fs(16);
                line-height: 16px;
                text-align: center;
                position: relative;
                i {
                    position: absolute;
                    left: 15px;
                }
                p {
                    width: 150px;
                    height: 16px;
                    margin-top: 4px;
                    border-radius: 8px;
                    background: #fff;
                    font-family: $font-base;
                    font-weight: 500;
                    font-size: 10px;
                    line-height: 16px;
                    text-align: center;
                }
                &:first-child {
                    background: #337ab7;
                    p {
                        color: #337ab7;
                    }
                }
                &:last-child {
                    background: #FF871C;
                    p {
                        color: #FF871C;
                    }
                }
            }
        }
        &-middle {
            padding: 10px 0;
            a {
                display: block;
                width: 100%;
                text-align: center;
            }
            img {
                max-width: 1062px;
                width: 100%;
            }
            a:hover {
                opacity: .7;
            }
        }
        &-bottom {
            background-color: $color-green;
            color: white;
            @include mq() {
                margin-top: 10px;
                padding: 0;
            }
            ul {
                display: flex;
                // justify-content: space-between;
                // align-items: end;
                li {
                    font-family: $font-a;
                    font-weight: 500;
                    font-size: 12px;
                    line-height: 16px;
                    &:nth-child(5) {
                        width: calc(100% - 600px);
                        @include mq(tb2) {
                            width: calc(100% - 360px);
                        }
                        @include mq(sm2) {
                            width: calc(100% - 240px);
                        }
                        @include mq(smt) {
                            width: 100%;
                        }
                    }
                    a {
                        padding: 10px;
                        display: block;
                        text-align: center;
                        color: white;
                        &:hover {
                            color: aquamarine;
                            background: linear-gradient(rgba(255, 255, 255, 0) 0%, #fff6 50%, rgba(255, 255, 255, 0) 100%);
                        }
                    }
                    input {
                        width: 95%;
                        margin-top: 5px;
                        border-radius: 7px;
                        border-style: hidden;
                        padding: 8px 10px;
                        background: #fff;
                        font-family: $font-base;
                        font-size: 15px;
                        color: $color-main;
                        line-height: 16px;
                        @include mq(sm2) {
                            width: 95%;
                            margin-top: 0px;
                            margin-bottom: 5px;
                        }
                    }
                    center {
                        margin-top: 5px;
                        @include mq(sm2) {
                            margin-top: 10px;
                        }
                        @include mq(smt) {
                            margin-bottom: 5px;
                        }
                    }
                    &.break {
                        width: 1px;
                        height: 52px;
                        padding: 0;
                        background: linear-gradient(rgba(255, 255, 255, 0) 0%, #fff 50%, rgba(255, 255, 255, 0) 100%);
                        &::after {
                            content: unset;
                        }
                    }
                    &.select {
                        width: 120px;
                        span {
                            &::after {
                                content: url(../images/common/arrow-down.svg);
                                display: block;
                                text-align: center;
                            }
                        }
                    }
                }
            }
        }
    }
}