@charset "UTF-8";

/* -------------------
	PAGINATION
--------------------*/

.pagination {
    margin: 0px auto 20px;
    // @include mq() {
    //     margin: 50px auto;
    // }
    &-label {
        margin-bottom: 7px;
        font-size: fs(10);
        color: #000;
        text-align: center;
    }
    &-list {
        display: flex;
        text-align: center;
        flex-flow: wrap;
        justify-content: center;
        a {
            width: fs(30);
            height: fs(30);
            display: flex;
            margin: 0 fs(2);
            justify-content: center;
            align-items: center;
            border-radius: 50%;
            background-color: #F8F9FA;
            color: #1F8CFA;
            font-family: $font-d;
            font-size: fs(12);
            letter-spacing: 0;
            line-height: 1.4;
            transition: all .25s;
            @include mq() {
                margin-bottom: 5px;
            }
            &:hover {
                background: linear-gradient(270deg, #1F8CFA 0%, #0566EF 100%) 0% 0%;
                color: #fff;
            }
            &.active {
                background: linear-gradient(270deg, #1F8CFA 0%, #0566EF 100%) 0% 0%;
                color: #fff;
                pointer-events: none;
            }
            &.ctrl {
                padding: 0 11px;
                &:hover {
                    svg {
                        path {
                            fill: #fff;
                        }
                    }
                }
                &.prev {
                    margin-right: 8px;
                }
                &.next {
                    margin-left: 8px;
                }
            }
        }
    }
    &-spacer {
        display: flex;
        justify-content: center;
        align-items: center;
        width: .875rem;
        color: #919191;
        margin: .125rem .0625rem;
        height: 100%;
    }
}