main {
    margin-top: 15px;
    display: flex;
    ul li a {
        display: block;
        &:hover {
            color: red;
        }
    }
}

.hover_panel {
    &.show {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, .5);
        z-index: 1;
        transition: .3s ease-out;
    }
}

#sidebar {
    width: 220px;
    background: white;
    @include mq(sm2) {
        position: absolute;
        left: -230px;
    }
    &.show {
        left: 0;
        transition: .3s ease-out;
        position: fixed !important;
        top: 0 !important;
        left: 0 !important;
        height: 100vh;
        overflow-y: scroll;
        z-index: 2;
    }
    .creteria {
        margin-top: 15px;
        padding: 5px;
        background: #f6f5f0;
        &:first-child {
            margin-top: 0px;
        }
    }
    .title {
        padding: 5px;
        font-family: $font-b;
        font-weight: 500;
        font-size: fs(16);
        line-height: 25px;
        color: #fff;
        background: $color-green;
    }
    .target-name {
        padding: 5px;
        font-family: $font-a;
        font-weight: 500;
        font-size: 13px;
        line-height: 16px;
        text-align: left;
        color: #000;
        span {
            margin-top: 5px;
            font-family: $font-b;
            font-size: fs(16);
        }
    }
    ul li {
        padding: 5px;
        border-top: 1px solid #aaa485;
        font-family: $font-base;
        font-size: 13px;
        position: relative;
        span {
            padding: 3px;
            border-radius: 4.5px;
            background: #ffa401;
            color: white;
            font-size: 11px;
            position: absolute;
            right: 3px;
        }
    }
}

#main-wrapper {
    width: calc(100% - 240px);
    padding-left: 40px;
    @include mq(sm2) {
        width: 100%;
        padding-left: 0;
    }
}

#tab1 {}

.category-block {
    width: 31%;
    margin: 10px 20px 0 0;
    display: inline-block;
    @include mq(sm2) {
        width: 44%;
        margin: 10px 10px 0 0;
    }
    @include mq(sm3) {
        width: 95%;
    }
    &-name {
        border-bottom: 1px solid $color-gray;
        font-family: $font-a;
        font-weight: bold;
        font-size: fs(16);
        line-height: 30px;
        text-align: left;
        color: black;
    }
    li {
        font-family: $font-base;
        font-weight: 500;
        font-size: fs(13);
        line-height: 30px;
        text-align: left;
        color: $color-main;
    }
}

.side_show {
    position: fixed;
    top: 50%;
    left: -22px;
    z-index: 11;
    padding: 5px;
    background-color: #333;
    transform: rotate( 90deg);
    letter-spacing: 3px;
    a {
        color: white;
        font-size: 13px;
    }
}

.ranking {
    margin-top: 50px;
    .title {
        font-family: $font-a;
        font-weight: bold;
        font-size: fs(16);
        line-height: 40px;
        color: black;
    }
}

#category-product-list .tabs {
    ul li {
        min-width: 150px;
    }
}

#information {
    border: 1px solid #ededed;
    padding: 20px 30px 5px;
    margin: 0 auto 15px;
    border-radius: 3px;
    .body {
        .title {
            color: #db0000;
            font-weight: 500;
            display: table-cell;
            vertical-align: middle;
            padding-right: 30px;
            @include mq(sm2) {
                display: block;
                padding: 0 0 20px;
            }
        }
        .text {
            display: table-cell;
            word-break: break-all;
            font-size: 15px;
        }
    }
}

.slide {
    padding: 50px 0;
    @include mq(sm2) {
        padding: 10px 0;
    }
}

.wideslider {
    width: 100%;
    text-align: left;
    position: relative;
    overflow: hidden;
    .pagination {
        @include mq(sm2) {
            display: none;
        }
    }
    .wideslider_base {
        top: 80px;
        position: relative;
        @include mq(sm2) {
            top: 0;
        }
    }
    .pagination {
        display: flex;
        align-items: center;
        justify-content: center;
        @include mq(sm2) {
            display: none !important;
        }
    }
}

#scene {
    background: #f4fafb;
    .itemBox {
        ul {
            grid-template-columns: 1fr 1fr 1fr 1fr;
            @include mq(sm2) {
                grid-template-columns: 1fr 1fr;
            }
            li {
                padding: 0;
                border: 1px solid #afcfd4;
                display: block;
            }
            .label {
                border: none;
                font-size: 14px;
                text-align: center;
                font-weight: 500;
                padding-bottom: 20px;
            }
            a {
                color: #000;
                text-decoration: none;
                display: block;
            }
            .image {
                border-bottom: none;
                img {
                    border-bottom: 1px solid #afcfd4;
                }
            }
        }
    }
}

#faq {
    border-top: 3px solid #ff0000;
    background: #FFF !important;
    h1 {
        text-align: center;
        font-size: 24px;
        font-weight: bold;
        position: relative;
        margin-bottom: 50px;
    }
    dl {
        dt {
            font-size: 16px;
            font-weight: 700;
            padding: 0 0 10px 1.5em;
            position: relative;
            &:before {
                content: "Q.";
                font-size: 18px;
                color: #ff5d5d;
                display: block;
                width: 1.5em;
                position: absolute;
                top: 0;
                left: 0;
            }
        }
        dd {
            padding: 0 0 30px 1.5em;
            margin: 0 0 30px 0;
            border-bottom: 1px solid #ededed;
            position: relative;
            &:before {
                content: "A.";
                color: #447d8f;
                font-size: 16px;
                font-weight: 700;
                display: block;
                width: 1.5em;
                position: absolute;
                top: 0;
                left: 0;
            }
        }
    }
}

.category-2 {
    &-title {
        margin-top: 15px;
        margin-bottom: 15px;
        font-size: fs(22);
    }
    &-detail {
        font-size: fs(14);
        line-height: 25px;
    }
    .products-list {
        margin-top: 20px;
        li {
            border-top: 1px solid #ededed;
            padding: 12px 0;
            display: flex;
            align-items: center;
            justify-content: space-between;
            @include mq(tb2) {
                flex-wrap: wrap;
                .image {
                    width: 150px !important;
                    order: 1;
                }
                .hidden {
                    width: calc(100% - 300px);
                    display: block !important;
                    order: 2;
                    .review span {
                        font-size: 13px;
                    }
                }
                .extra {
                    width: 80px !important;
                    order: 3;
                }
                .detail {
                    width: 100% !important;
                    order: 4;
                }
            }
            @include mq(smt) {
                flex-wrap: wrap;
                .image {
                    width: 200px !important;
                    order: 1;
                }
                .extra {
                    width: 100px !important;
                    order: 2;
                }
                .detail {
                    width: 100% !important;
                    order: 3;
                }
            }
            .image {
                width: 15%;
                img {
                    width: 100%;
                }
            }
            .hidden {
                display: none;
            }
            .detail {
                width: 60%;
                .brand {
                    margin-bottom: 10px;
                    img {
                        height: 15px;
                        width: auto;
                    }
                    @include mq(tb2) {
                        display: none;
                    }
                }
                .name {
                    font-size: fs(20);
                    margin-bottom: 10px;
                }
                .review {
                    margin-bottom: 10px;
                    display: flex;
                    align-items: center;
                    span {
                        margin-left: 15px;
                        font-size: 13px;
                    }
                    @include mq(tb2) {
                        display: none;
                    }
                }
                .description {
                    font-size: 14px;
                    font-family: $font-a;
                    line-height: 20px;
                }
                dl {
                    margin-bottom: 10px;
                    display: flex;
                    align-items: center;
                    flex-wrap: wrap;
                    dt {
                        margin-top: 10px;
                        margin-right: 10px;
                        border: 1px solid #e2e2e2;
                        border-radius: 3px;
                        padding: 0px 5px;
                        background: #f2f2f2;
                        font-family: $font-b;
                        font-size: 13px;
                    }
                    dd {
                        margin-top: 10px;
                        font-size: 13px;
                        margin-right: 15px;
                    }
                }
            }
            .extra {
                width: 15%;
                .price-no-tax {
                    margin-bottom: 8px;
                    font-size: 16px;
                    font-weight: bold;
                    color: red;
                }
                .price-with-tax {
                    margin-bottom: 8px;
                    font-size: 13px;
                }
                .delivery span {
                    margin-bottom: 8px;
                    border-radius: 3px;
                    padding: 3px 5px;
                    font-size: 13px;
                    color: white;
                    background: red;
                }
            }
        }
    }
}

#privacy {
    h2 {
        margin-bottom: 20px;
        font-weight: bold;
        font-size: fs(25);
        line-height: 30px;
        text-align: left;
        color: #000;
    }
    p {
        margin-bottom: 15px;
        font-size: 14px;
        font-weight: 200;
        line-height: 25px;
        color: $color-main;
        &.stage1 {
            border-bottom: 1px solid #e2e2e2;
            padding-bottom: 3px;
            font-weight: bold;
            font-size: fs(20);
            line-height: 25px;
            text-align: left;
            color: #a80503;
        }
        &.stage2 {
            border-bottom: 1px solid #e2e2e2;
            padding-bottom: 3px;
            font-weight: bold;
            font-size: fs(17);
            line-height: 25px;
            text-align: left;
            color: $color-main;
        }
        &.stage3 {
            font-size: 14px;
            font-weight: 200;
            line-height: 25px;
            color: $color-main;
        }
    }
}

#agreement {
    h2 {
        margin-bottom: 20px;
        font-weight: bold;
        font-size: fs(25);
        line-height: 30px;
        text-align: left;
        color: #000;
    }
    p {
        margin-bottom: 15px;
        font-size: 14px;
        font-weight: 200;
        line-height: 25px;
        color: $color-main;
        &.stage1 {
            border-bottom: 1px solid #e2e2e2;
            padding-bottom: 3px;
            font-weight: bold;
            font-size: fs(20);
            line-height: 25px;
            text-align: left;
            color: #a80503;
        }
        &.stage2 {
            font-weight: bold;
            font-size: fs(17);
            line-height: 25px;
            text-align: left;
            color: $color-main;
        }
    }
}

#specific {
    h2 {
        margin-bottom: 20px;
        font-weight: bold;
        font-size: fs(25);
        line-height: 30px;
        text-align: left;
        color: #000;
    }
    table,
    th,
    td {
        border: 1px solid #e2e2e2;
    }
    th {
        font-weight: bold;
        font-size: fs(15);
        line-height: 25px;
        text-align: left;
        color: $color-main;
        vertical-align: middle;
        padding: 10px 20px;
    }
    td {
        font-size: 13px;
        line-height: 25px;
        text-align: left;
        color: $color-main;
        padding: 10px 20px;
        ul {
            padding-left: 15px;
            list-style-type: disc;
        }
    }
}